.Main {
    width: 100%;
    height: 500px;
    margin: 50px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #09080d;
}

.Img {
    width: 50%;
}

.Main img {
    /* height: 430px; */
    width: 100%;
}

.Heading {
    color: white;
    height: 250px;
    width: 50%;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    align-items: center;
    letter-spacing: 4px;
}

.Heading h1 {
    color: #B4A5A5;
}

.Heading h2 {
    color: #EBEBEB;
}

.Heading button {
    height: 40px;
    width: 200px;
    border-radius: 20px;
    background-color: #7d7990;
    border: 1px solid transparent;
    color: #e7eaf1;
    letter-spacing: 1px;
    cursor: pointer;
}

.Heading button:hover {
    background-color: #6f659e;
    color: #ffffff;
    transform: scale(1.1);
}

.Menu {
    display: flex;
    width: 99%;
    margin: 50px auto;
    /* padding: 10px; */
    flex-flow: column;
    height: 400px;
    justify-content: space-evenly;
    align-items: center;
    background-color: #334257;
    border-radius: 20px;
}

.Menu h1 {
    color: #c8cccf;
}

.SubMenu {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.SubMenu a {
    text-decoration: none;
    color: #c8cccf;
}

.SubMenu a:hover {
    color: #ffffff;
    transform: scale(1.1);
}

@media (max-width: 799px) {
    .Heading {
        background-image: url('../../../Assets/Images/phoneMain2.jpg');
        background-position: center;
        background-size: cover;
        height: 100%;
        width: 100%;
        padding: 10px;
    }

    .Img {
        display: none;
    }

    .Main {
        justify-content: center;
    }

    .SubMenu a {
        font-size: 13px;
    }
}

@media (max-width: 559px) {
    .Menu {
        height: auto;
    }

    .SubMenu {
        height: 380px;
        flex-flow: column;
    }

    
}